import { useIsSignedIn } from "../../../utils/hooks/use_is_signed_in";
import { SignedInDesktopMenu, SignedOutDesktopMenu } from "./toogle_menu";

export function Menu() {
  const { isSignedIn } = useIsSignedIn();
  // retrieve
  return isSignedIn ? <SignedInDesktopMenu /> : <SignedOutDesktopMenu />;
}
