import React from "react";
import { cn } from "../../utils/custom_functions";

export function Button({ className, children, ...props }) {
  return (
    <button
      {...props}
      className={cn(
        "w-full bg-yellow-400 font-bold px-3 py-2 rounded-md text-black disabled:bg-yellow-200 disabled:text-opacity-50 disabled:pointer-events-none ",
        className
      )}
    >
      {children}
    </button>
  );
}
